}

.btn .svgIcon {
	vertical-align: middle;
	fill: rgba(255, 255, 255, 0.54);
	padding-right: 4px;
	height: 37px;
	display: inline-block;
}
* {
  padding: 0;
  margin: 0;
  list-style: none;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
}
.bg {
  width: 100%;
  height: 100%;
  margin: 0;
}
.sidebar {
  position: fixed;
  left: -250px;
  width: 250px;
  height: 100%;
  background: #042331;
  transition: all 0.5s ease;
}
.sidebar header {
  font-size: 22px;
  color: white;
  line-height: 70px;
  text-align: center;
  background: #063146;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.sidebar ul a {
  display: block;
  height: 100%;
  width: 100%;
  line-height: 65px;
  font-size: 20px;
  color: white;
  padding-left: 40px;
  box-sizing: border-box;
  border-bottom: 1px solid black;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  transition: 0.4s;
}
ul li:hover a {
  padding-left: 50px;
}
.sidebar ul a i {
  margin-right: 16px;
}
#check {
  display: none;
}
label #btn,
label #cancel {
  position: absolute;
  background: #042331;
  border-radius: 3px;
  cursor: pointer;
}
label #btn {
  left: 40px;
  top: 25px;
  font-size: 35px;
  color: white;
  padding: 6px 12px;
  transition: all 0.5s;
}
label #cancel {
  z-index: 1111;
  left: -195px;
  top: 17px;
  font-size: 30px;
  color: #0a5275;
  padding: 4px 9px;
  transition: all 0.5s ease;
}
#check:checked ~ .sidebar {
  left: 0;
}
#check:checked ~ label #btn {
  left: 250px;
  opacity: 0;
  pointer-events: none;
}
#check:checked ~ label #cancel {
  left: 195px;
}
#check:checked ~ section {
  margin-left: 250px;
}

/* DAY STYLES*/

.the-container {
  display: block;
  position: absolute;
  width: 500px;
  height: 350px;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.c-window {
  display: block;
  position: relative;
  width: 235px;
  height: 235px;
  margin: 0 auto;
  border-radius: 100%;
  border: 8px solid #063146;
  background: #dddddd;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25) inset;
  overflow: hidden;
  transition: all 0.5s linear;
  -webkit-transition: all 0.5s linear;
}

.c-window .the-sun {
  display: block;
  position: relative;
  top: 18px;
  height: 40px;
  width: 40px;
  background: #ffe067;
  border-radius: 100%;
  margin: 0 auto;
  left: 30px;
}

.c-window .the-moon {
  position: relative;
  height: 24px;
  width: 24px;
  background: #eee;
  border-radius: 100%;
  display: none;
}

.c-window .the-fox {
  display: block;
  position: absolute;
  bottom: -20px;
  height: 140px;
  width: 135px;
  margin: 0 50px;
  background: #e86a47;
  transition: bottom 1s;
  -webkit-transition: bottom 0.15s ease-in-out;
}

.c-window .the-fox:before {
  width: 0;
  height: 0;
  border-left: 0px solid transparent;
  border-right: 60px solid transparent;
  border-bottom: 30px solid #e86a47;
  top: -25px;
  left: 0;
  position: absolute;
  content: '';
}
.c-window .the-fox:after {
  width: 0;
  height: 0;
  border-right: 0px solid transparent;
  border-left: 60px solid transparent;
  border-bottom: 30px solid #e86a47;
  top: -30px;
  right: 0;
  position: absolute;
  content: '';
}

.c-window .the-fox:hover {
  bottom: -30px;
}

.c-window .the-fox .eyes {
  display: block;
  position: absolute;
  background: #ffffff;
  height: 15px;
  width: 15px;
  border-radius: 100%;
  bottom: 90px;
  --webkit-transition: all 0.15s linear;
}

.c-window .the-fox:hover .eyes {
  height: 2px;
  bottom: 97px;
}

.c-window .the-fox .eyes.left {
  left: 30px;
}

.c-window .the-fox .eyes.right {
  right: 30px;
}

.c-window .the-fox .nose {
  display: block;
  position: relative;
  background: #333;
  height: 12px;
  width: 12px;
  border-radius: 100%;
  margin: 0 auto;
  top: 50px;
}

.c-window .the-fox .white-part {
  display: block;
  position: relative;
  width: 0px;
  height: 0px;
  top: 55px;
  border-style: solid;
  border-width: 60px 70px 0 65px;
  border-color: #ffffff transparent transparent transparent;
}

.input2[type='checkbox'] {
  position: absolute;
  visibility: hidden;
}

.input2#toggle[type='checkbox'] {
  display: none;
}

.label2 {
  position: absolute;
  height: 40px;
  width: 120px;
  display: block;
  top: 0px;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 9999;
  cursor: pointer;
  margin: 0 auto;
}

.switch {
  display: block;
  position: relative;
  border-bottom: 1px solid #fff;
  border-radius: 25px;
  background: #063146;
  box-shadow: inset 0 0 10px #888888;
  -webkit-box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.25);
  height: 40px;
  width: 100px;
  margin: 0px auto 30px auto;
}

.switch .button {
  display: block;
  position: absolute;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #aaa;
  border-radius: 100%;
  background: #48e8aa;
  height: 32px;
  width: 32px;
  top: 4px;
  left: 4px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.25);
}

.switch .button .b-inside {
  display: block;
  position: absolute;
  border: 1px solid #888;
  border-radius: 100%;
  background: #ffe067;
  height: 15px;
  width: 15px;
  top: 7px;
  left: 7px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.25);
}

.day-night-cont {
  display: block;
  position: absolute;
  width: 180px;
  margin: 0 auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 40px;
  top: 0px;
}

.day-night-cont .the-sun {
  display: block;
  position: absolute;
  left: 10px;
  top: 10px;
  height: 20px;
  width: 20px;
  border-radius: 100%;
  background: #ffe067;
  box-shadow: 0px 0px 10px #ffc41d;
}

.day-night-cont .the-moon {
  display: block;
  position: absolute;
  right: 8px;
  top: 10px;
  height: 20px;
  width: 20px;
  border-radius: 100%;
  background: #ddd;
  box-shadow: 0px 0px 50px #ccc;
}

.day-night-cont .the-moon .moon-inside {
  display: block;
  position: absolute;
  left: 8px;
  height: 20px;
  width: 20px;
  border-radius: 100%;
  background: #ffffff;
}

/* TOGGLE */

.switch .button {
  transition: left 0.25s ease-in-out;
  -webkit-transition: left 0.25s ease-in-out;
}

.input2[type='checkbox']:checked ~ .switch .button {
  position: absolute;
  left: 64px;
}

/* NIGHT ANIMATION */

.input2[type='checkbox']:checked ~ .c-window {
  background: #222222;
}

.input2[type='checkbox']:checked ~ .c-window .the-sun {
  display: none;
}
.input2[type='checkbox']:checked ~ .c-window .the-moon {
  display: block;
  position: absolute;
  margin: 0 auto;
  top: 40px;
  left: 60px;
}

.input2[type='checkbox']:checked ~ .c-window .the-fox {
  background: #c74628;
}

.input2[type='checkbox']:checked ~ .c-window .the-fox:before {
  width: 0;
  height: 0;
  border-left: 0px solid transparent;
  border-right: 60px solid transparent;
  border-bottom: 30px solid #c74628;
  top: -30px;
  left: 0;
  position: absolute;
  content: '';
}
.input2[type='checkbox']:checked ~ .c-window .the-fox:after {
  width: 0;
  height: 0;
  border-right: 0px solid transparent;
  border-left: 60px solid transparent;
  border-bottom: 30px solid #c74628;
  top: -30px;
  right: 0;
  position: absolute;
  content: '';
}

.input2[type='checkbox']:checked ~ .c-window .the-fox .eyes {
  height: 2px;
  bottom: 90px;
}

.input2[type='checkbox']:checked ~ .c-window .the-fox:hover .eyes {
  height: 15px;
  bottom: 85px;
}

